<template lang="html">
  <canvas id="df-dot"></canvas>
</template>

<script>
import './js'
export default {
  name: 'dot',
  props: {
    numDot: {
      type: Number,
      default: 50
    },
    radDot: {
      type: Number,
      default: 75
    },
    isLine: {
      type: Boolean,
      default: true
    },
    lineDist: {
      type: Number,
      default: 200
    },
    opacity: {
      type: Number,
      default: 0.5
    },
    vxRange: {
      type: Number,
      default: 1
    },
    vyRange: {
      type: Number,
      default: 1
    }
  },
  mounted() {
    window.Dot('df-dot', {
      // canvas 
      cW: window.innerWidth,
      cH: window.innerHeight,

      // 
      numDot: this.numDot,

      // 
      radDot: this.radDot,

      // 
      isRangeRad: true,

      // 
      dotColor: '#42b983',

      // 
      isLine: this.isLine,

      // 
      lineDist: this.lineDist,

      // 
      lineColor: '#42b983',

      // 
      bounce: 1,

      // 
      opacity: this.opacity,

      // 
      isTouch: true,

      // x
      vxRange: this.vxRange,

      // y
      vyRange: this.vyRange,

      // 
      isWallCollisionTest: true,

      // 
      isBallCollisionTest: true
    })
  }
}
</script>

<style lang="stylus" scoped>
  #df-dot
    position absolute
    z-index -1
</style>



