<template lang="html">
  <section class="df-feedback">
    <div class="main-wrapper">
      <div class="main">
        <div class="declaration">
          BUG<br/>
          <br />
          :)
        </div>
        <el-form class="form c-form" ref="form" :model="form" :rules="rules">
          <div class="info">
            <a class="tdu" href="http://chenlongfei.me" target="_blank">
              <i class="fa fa-home"></i>
            </a>
            <a class="tdu" href="https://github.com/DragonFlyXD" target="_blank">
              <i class="fa fa-github"></i>GitHub
            </a>
            <a class="tdu" href="http://music.163.com/#/user/home?id=323933339" target="_blank">
              <i class="fa fa-music"></i>
            </a>
            <el-tooltip content="dragonfly920130@outlook.com" effect="light">
              <a class="tdu" href="#"><i class="fa fa-envelope-o"></i></a>
            </el-tooltip>
            <el-tooltip>
              <div slot="content">
                <img class="wechat" src="http://images.dragonflyxd.com/wechat.jpg" alt="wechat">
              </div>
              <a class="tdu" href="#"><i class="fa fa-wechat"></i></a>
            </el-tooltip>
          </div>
          <div class="tip"></div>
          <el-form-item prop="subject">
            <el-input
              placeholder=""
              v-model="form.subject"
            ></el-input>
          </el-form-item>
          <el-form-item prop="body">
            <quill-editor
              class="c-quill"
              :options="editorOptions"
              v-model="form.body"
            ></quill-editor>
          </el-form-item>
          <el-form-item>
            <el-button
              class="send"
              :loading="isSending"
              :disabled="isSent"
              @click="feedbackShipped">
              <i class="fa fa-paper-plane-o"></i>
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </section>
</template>

<script>
import {quillEditor} from 'vue-quill-editor'
import api from '@/api'
export default {
  name: 'feedback',
  components: {
    quillEditor
  },
  data() {
    return {
      editorOptions: { // quill 
        theme: 'bubble',
        placeholder: 'Markdown'
      },
      form: { // 
        subject: '',
        body: ''
      },
      rules: { // 
        subject: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 4,
            max: 120,
            message: '4-120',
            trigger: 'blur'
          }
        ],
        body: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      },
      // 
      isSending: false,
      // 
      isSent: false
    }
  },
  methods: {
    // 
    feedbackShipped() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.isSending = true
          api.post('email/feedback', this.form).then(response => {
            this.isSending = false
            this.isSent = true
            this.$notify({
              title: '',
              message: '!',
              type: 'success',
              customClass: 'c-msg'
            })
          }).catch(error => {
            this.isSending = false
            this.isSent = true
            this.$message({
              message: '/(o)/~~',
              type: 'error',
              customClass: 'c-msg',
              duration: 0,
              showClose: true
            })
            Promise.reject(error)
          })
        }
        return false
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '../../common/stylus/common'

.df-feedback
  margin-top 20px
  .main-wrapper
    fj(center)
    .main
      width 50%
      .declaration
        padding 10px
        border 1px solid Light-Silver
        border-radius 5px
        box-shadow 0px 1px 1px rgba(0,0,0,.2)
        text-align center
        line-height 2em
        color Silver
      .form
        .info
          fj(center)
          margin 20px 0
          padding 10px 0
          border 1px solid Light-Silver
          border-radius 10px
          a
            margin-right 20px
            &>i
              margin-right 5px
        .tip
          margin 20px 0
          padding 10px 0
          text-align center
          border-radius 10px
          bc(Dark-White,Silver)
        .send
          wh(100%,50px)
          border-radius 50px
          font-size 1.5em
          text-align center
.wechat
  wh(200px,200px)
</style>



