import * as types from './mutation-types'
import api from '@/api'
import {Message} from 'element-ui'

export default {
  // 
  logout({commit}) {
    commit(types.TOGGLE_LOADING_STATUS)
    api.post('user/logout').then(response => {
      commit(types.TOGGLE_LOADING_STATUS)
      // 
      if (response.data.logout) {
        //  localstorage  ACCESS_TOKEN
        commit(types.USER_LOGOUT)
        // 
        commit(types.CHECKOUT_LOGIN_STATUS)
        Message({message: '', type: 'success', customClass: 'c-msg'})
      }
    }).catch(error => {
      commit(types.TOGGLE_LOADING_STATUS)
      Message({message: '/(o)/~~', type: 'error', customClass: 'c-msg', duration: 0, showClose: true})
      Promise.reject(error)
    })
  }
}



