<template lang="html">
  <div class="df-emailVerify">
    <div class="alert">
      <el-alert
        title=""
        type="success"
        description=" :)"
        v-if="isSuccess"
      ></el-alert>
      <el-alert
        title=""
        type="error"
        description="/(o)/~~"
        v-else
      ></el-alert>
    </div>
    <div class="poem">
      <h3 class="title">   </h3>
      <div class="main">
        <div class="box">
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </div>
        <div class="box">
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </div>
        <div class="box">
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </div>
        <div class="box">
          <p></p>
        <p></p>
          <p></p>
          <p></p>
        </div>
        <div class="box">
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </div>
        <div class="box">
          <p></p>
          <p></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  mapMutations
} from 'vuex'
import api from '@/api'
export default {
  name: 'emailVerify',
  data() {
    return {
      // 
      isSuccess: false
    }
  },
  created() {
    // toke
    this.validateToken()
  },
  methods: {
    validateToken() {
      this.TOGGLE_LOADING_STATUS()
      // token
      const token = this.$router.currentRoute.query.token
      if (!token) {
        // token
        this.isSuccess = false
        setTimeout(() => {
          this.TOGGLE_LOADING_STATUS()
        }, 0)
      } else {
        // token
        api.get('email/verify?token=' + token).then(response => {
          this.TOGGLE_LOADING_STATUS()
          this.isSuccess = response.data.verified
        }).catch(error => {
          this.TOGGLE_LOADING_STATUS()
          Promise.reject(error)
        })
      }
    },
    ...mapMutations([
      'TOGGLE_LOADING_STATUS',
      'CHECKOUT_LOGIN_STATUS'
    ])
  }
}
</script>
<style lang="stylus" scoped>
@import '../../common/stylus/common'

.df-emailVerify
  .alert
    margin-top 10px
    text-align center
  .poem
    position fixed
    top 50%
    left 50%
    width 100%
    transform translate(-50%,-50%)
    .title
      text-align center
      color Green
    .main
      fj(space-around)
      .box
        color Silver
</style>



