<template lang="html">
  <div class="df-profile">
    <header class="header">
      <div class="left">
        <div class="avatar">
          <img class="now-avatar" :src="profile.avatar" alt="avatar">
          <el-upload
            class="update-avatar"
            :name="uploadOptions.name"
            :action="uploadOptions.action"
            :headers="uploadOptions.headers"
            :show-file-list="uploadOptions.showFileList"
            :before-upload="beforeAvatarUpload"
            :on-success="handleAvatarSuccess"
            :on-error="handleAvatarError"
            v-if="isOwns"
          >
            <p> :)</p>
          </el-upload>
        </div>
      </div>
      <div class="mid">
        <h1 class="name">{{ profile.nickname }}</h1>
        <div class="info">
          <div class="box">
            <span></span>
            <span>{{ profile.works_count }}</span>
          </div>
          <div class="box">
            <span></span>
            <span>{{ profile.followers_count }}</span>
          </div>
          <div class="box">
            <span></span>
            <span>{{ profile.followings_count }}</span>
          </div>
          <el-button class="btn-pub" @click="toggleProfileDialog" v-if="isOwns"></el-button>
        </div>
      </div>
      <div class="right">
        <el-progress
          class="c-progress"
          type="circle"
          :percentage="completion"
          :stroke-width="8"
          :width="200"
        ></el-progress>
      </div>
    </header>
    <div class="body">
      <dot></dot>
      <div class="content">
        <div class="box">
          <h4></h4>
          <span>{{ profile.nickname }}</span>
        </div>
        <div class="box">
          <h4></h4>
          <span>{{ profile.gender }}</span>
        </div>
        <div class="box">
          <h4></h4>
          <span>{{ profile.birthday }}</span>
        </div>
        <div class="box">
          <h4></h4>
          <span class="f-taj">{{ profile.signature }}</span>
        </div>
        <div class="box">
           <h4></h4>
          <span>{{ profile.location }}</span>
        </div>
        <div class="box">
          <h4></h4>
          <span>{{ profile.occupation }}</span>
        </div>
        <div class="box">
          <h4></h4>
          <span class="f-taj">{{ profile.bio }}</span>
        </div>
        <div class="box">
          <h4></h4>
          <span>{{ profile.poet }}</span>
        </div>
      </div>
    </div>
    <el-dialog title="" :visible.sync="dialogVisible" custom-class="profile-dialog c-dialog">
      <div class="form-wrapper">
        <el-form class="c-form form" label-width="100px" ref="profile" :model="profile" :rules="rules">
          <el-form-item label="" prop="nickname">
            <el-input
            v-model="profile.nickname"
            :placeholder="placeholder.nickname"
            @focus="hidePlaceholder"
            @blur="showPlaceholder"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="gender">
            <el-radio-group v-model="profile.gender" fill="#42b983" text-color="rgba(66,185,131,.8)">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="birthday">
            <el-date-picker
              v-model="profile.birthday"
              type="date"
              :placeholder="placeholder.birthday"
              :editable="false"
              :picker-options="pickerOptions"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="signature">
            <el-input
            type="textarea"
            v-model="profile.signature"
            :placeholder="placeholder.signature"
            :rows="2"
            @focus="hidePlaceholder"
            @blur="showPlaceholder"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="location">
            <el-input
            :placeholder="placeholder.location"
            v-model="profile.location"
            @focus="hidePlaceholder"
            @blur="showPlaceholder"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="occupation">
            <el-input
              :placeholder="placeholder.occupation"
              v-model="profile.occupation"
              @focus="hidePlaceholder"
              @blur="showPlaceholder"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="bio">
            <el-input
              type="textarea"
              v-model="profile.bio"
              :placeholder="placeholder.bio"
              :autosize="{minRows:4,maxRows:20}"
              @focus="hidePlaceholder"
              @blur="showPlaceholder"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="poet">
            <el-input
              :placeholder="placeholder.poet"
              v-model="profile.poet"
              @focus="hidePlaceholder"
              @blur="showPlaceholder"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button class="publish" @click="submitProfile"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import api from '@/api'
import Dot from '@/components/dot'
import {
  mapGetters,
  mapActions,
  mapMutations
} from 'vuex'
export default {
  name: 'userProfile',
  components: {
    Dot
  },
  data() {
    return {
      placeholder: {
        nickname: '',
        birthday: '1996-07-21',
        signature: '140',
        location: ' ()',
        occupation: ' ()',
        bio: ' ()',
        poet: '()'
      },
      rules: {
        nickname: [
          {
            min: 2,
            max: 20,
            message: '2-20',
            trigger: 'blur'
          },
          {
            type: 'string',
            pattern: /^[\u4E00-\u9FFFa-zA-Z0-9_-]{2,20}$/,
            message: '',
            trigger: 'blur'
          }
        ],
        signature: [{
          max: 50,
          message: '140',
          trigger: 'blur'
        }],
        location: [{
          max: 50,
          message: '50',
          trigger: 'blur'
        }],
        occupation: [{
          max: 50,
          message: '50',
          trigger: 'blur'
        }],
        bio: [{
          max: 400,
          message: '500',
          trigger: 'blur'
        }],
        poet: [{
          max: 50,
          message: '50',
          trigger: 'blur'
        }]
      },
      profile: {},  //  or 
      uploadOptions: {
        name: 'avatar',  // 
        action: 'http://www.dragonflyxd.com/api/user/avatar',  // 
        headers: {  // 
          'Authorization': 'Bearer ' + window.localStorage.ACCESS_TOKEN,
          'Accept': 'application/json'
        },
        showFileList: false   // 
      },  // 
      dialogVisible: false,
      lastPlaceholder: '',
      isOwns: false,
      completion: 0, // 
      matches: [  // 
        'nickname',  // 
        'gender',  // 
        'birthday',  // 
        'signature',  // 
        'location',  // 
        'occupation',  // 
        'bio',  // 
        'poet'  // 
      ],
      pickerOptions: {
        // 
        disabledDate(time) {
          return time.getTime() > Date.now()
        }
      }
    }
  },
  computed: {
    ...mapGetters({
      isLogined: 'isLogined',
      __profile: 'profile'
    })
  },
  created() {
    // 
    this.checkoutOwns()
    // 
    this.loadUserProfile(this.$router.currentRoute.path)
  },
  watch: {
    //    
    // 
    '$route' (to, from) {
      this.checkoutOwns()
      this.loadUserProfile(to.path)
    }
  },
  methods: {
    //  or 
    checkoutOwns() {
      if (this.$router.currentRoute.path === '/user/profile') {
        this.isOwns = true
      }
    },
    // 
    async loadUserProfile(path) {
      // 
      if (this.isOwns) {
        await this.loadProfile()
        this.profile = this.__profile
      } else {
        this.TOGGLE_LOADING_STATUS()
        await api.get(path).then(response => {
          this.TOGGLE_LOADING_STATUS()
          // 
          response.data.nickname = response.data.name
          // 
          this.profile = response.data
        }).catch(error => {
          this.TOGGLE_LOADING_STATUS()
          this.$message({message: '/(o)/~~', type: 'error', customClass: 'c-msg', duration: 0, showClose: true})
          Promise.reject(error)
        })
      }
      // 
      this.checkoutCompletion()
    },
    // // 
    checkoutCompletion() {
      let cnt = 0
      this.matches.forEach(item => {
        // 
        this.profile[item] && cnt++
      })
      // 
      this.completion = cnt * 100 / this.matches.length
    },
    // 
    beforeAvatarUpload(f) {
      const isJPGorPNG = f.type === 'image/jpeg' || f.type === 'image/png'
      const is500K = f.size / 1024 / 1024 < 0.5
      //  JPG or PNG
      if (!isJPGorPNG) {
        this.$message({
          message: 'JPGPNG',
          type: 'warning',
          customClass: 'c-msg'
        })
      }
      //  500k
      if (!is500K) {
        this.$message({
          message: '500K',
          type: 'warning',
          customClass: 'c-msg'
        })
      }
      return isJPGorPNG && is500K
    },
    // 
    handleAvatarError(err, f) {
      this.$message({message: '/(o)/~~', type: 'error', customClass: 'c-msg', duration: 0, showClose: true})
      Promise.reject(err)
    },
    // 
    handleAvatarSuccess(res, f) {
      // 
      res.avatar && this.updateAvatar(res.avatar)
    },
    // 
    submitProfile() {
      // 
      if (this.profile.birthday) {
        this.profile.birthday = this.dateFormatter(this.profile.birthday)
      }
      // 
      this.$refs['profile'].validate(async valid => {
        // 
        if (valid) {
          // 
          await this.updateProfile()
          this.profile = this.__profile
          // 
          this.checkoutCompletion()
          // 
          this.dialogVisible = false
        }
      })
    },
    dateFormatter(date) {
      return new Date(date).toLocaleDateString()
    },
    toggleProfileDialog() {
      this.dialogVisible = !this.dialogVisible
    },
    showPlaceholder(e) {
      e.target.placeholder = this.lastPlaceholder
    },
    hidePlaceholder(e) {
      this.lastPlaceholder = e.target.placeholder
      e.target.placeholder = ''
    },
    ...mapMutations([
      'TOGGLE_LOADING_STATUS'
    ]),
    ...mapActions([
      'loadProfile',
      'updateProfile',
      'updateAvatar'
    ])
  }

}
</script>
<style lang="stylus" scoped>
@import '../../common/stylus/common'

.df-profile
  .header
    fj(space-around)
    margin-top 20px
    padding-top 20px
    background-color Extra-Light-Grey
    .left
      width 16.6%
      .avatar
        position relative
        wh(200px,200px)
        border-radius 200px
        box-shadow 0 1px 1px rgba(0,0,0,.2)
        text-align center
        overflow hidden
        &:hover
          cursor pointer
          &>.update-avatar
            bottom 0
        .now-avatar
          wh(200px,200px)
        .update-avatar
          position absolute
          bottom -100px
          left 0
          wh(100%,100px)
          line-height 2em
          background linear-gradient(to bottom right,Extra-Light-Grey,White)
          color Green
          transition all .3s ease
          cursor pointer
          .el-upload__files
            display none
          p
            font-size .9em
    .mid
      width 25%
      text-align center
      letter-spacing 3px
      color Green
      .name
        &::before
          margin-right 20px
          content '\5B'
        &::after
          margin-left 20px
          content '\5D'
      .info
        margin-top 5px
        .box
          display inline-block
          margin-left 20px
          span
            display block
            font-size 1.3em
          & span:nth-child(1)
            margin-bottom 10px
            font-weight bold
          & span:nth-child(2)
            font-size 1.1em
        button
          margin-top 10px
    .right
      width 16.6%
      &:hover
        cursor pointer
  .body
    z-index 1
    fj(center,center)
    padding 20px 0
    .content
      width 33.3%
      .box
        text-align center
        overflow hidden
        span
          color Silver
  .profile-dialog
    .form-wrapper
      fj(center)
      .form
        width 75%
        .publish
          width 100%
</style>



