<template lang="html">
  <div class="df-poem">
    <div class="main">
      <template v-for="poem in this.poems">
        <sub-poem :poem="poem"></sub-poem>
      </template>
      <el-button
        class="spinner btn-default"
        v-show="spinnerVisible"
        :loading="isLoading"
      >Loading...</el-button>
      <span
        class="last-page"
        v-if="isLastPage"
      >:)</span>
    </div>
    <div class="side"></div>
  </div>
</template>

<script>
import SubPoem from '@/components/subPoem'
import {
  mapGetters,
  mapActions
} from 'vuex'
export default {
  name: 'poem',
  components: {
    SubPoem
  },
  data() {
    return {
      spinnerVisible: false,  // 
      isLoading: false,         // 
      isLastPage: false // 
    }
  },
  computed: {
    ...mapGetters([
      'poems',
      'poemLastPage',
      'poemCurrentPage'
    ])
  },
  // , scroll 
  beforeRouteLeave(to, from, next) {
    window.removeEventListener('scroll', this.next, false)
    next()
  },
  created() {
    // 
    !this.poemCurrentPage && this.loadPoemList()
    //  window  scroll 
    window.addEventListener('scroll', this.next, false)
  },
  methods: {
    // 
    async next() {
      //     
      if (document.body.scrollTop + window.innerHeight >= document.body.scrollHeight &&
        this.poemCurrentPage < this.poemLastPage && !this.isLoading) {
        // 50px
        document.querySelector('.df-poem').style.marginBottom = '50px'
        //    spinner
        this.spinnerVisible = this.isLoading = true
        //  Loading 
        await this.loadPoemList(true)
        //    spinner
        this.spinnerVisible = this.isLoading = false
        // 
        document.querySelector('.df-poem').style.marginBottom = '0'
      } else if (this.poemCurrentPage === this.poemLastPage && !this.isLastPage) {
        // 
        this.$message({
          message: ':)',
          type: 'warning',
          customClass: 'c-msg'
        })
        // 50px
        document.querySelector('.df-poem').style.marginBottom = '50px'
        this.isLastPage = true
      }
    },
    ...mapActions([
      'loadPoemList'
    ])
  }
}
</script>
<style lang="stylus" scoped>
@import '../../common/stylus/common'

.df-poem
  fj(center)
  margin-top 20px
  .main
    width 50%
    position relative
    margin-right 50px
    .spinner
      position absolute
      width 100%
    .last-page
      color Red
  .side
    width 20%
</style>



