import * as types from '../mutation-types'
import api from '@/api'
import {Message} from 'element-ui'
const state = {
  // 
  profile: {
    id: '',
    nickname: '', // 
    gender: '', // 
    birthday: '', // 
    signature: '',  // 
    location: '', // 
    occupation: '', // 
    bio: '',  // 
    poet: '', // 
    avatar: '' // 
  }
}
// 
let matches = [
  'nickname',
  'gender',
  'birthday',
  'signature',
  'location',
  'occupation',
  'bio',
  'poet'
]
const getters = {
  profile: state => state.profile
}
const actions = {
  // 
  async loadProfile({commit}) {
    commit(types.TOGGLE_LOADING_STATUS)
    await api.get('user').then(response => {
      commit(types.TOGGLE_LOADING_STATUS)
      // 
      response.data.nickname = response.data.name
      // 
      commit(types.LOAD_PROFILE, response.data)
    }).catch(error => {
      commit(types.TOGGLE_LOADING_STATUS)
      Message({message: '/(o)/~~', type: 'error', customClass: 'c-msg', duration: 0, showClose: true})
      Promise.reject(error)
    })
  },
  // 
  updateProfile({state, commit}) {
    commit(types.TOGGLE_LOADING_STATUS)
    // 
    let profile = {}
    matches.map(item => {
      // 
      if (item === 'gender') {
        if (state.profile[item]) {
          profile[item] = state.profile[item] === ''
            ? 1
            : 2
        } else {
          profile[item] = 0
        }
      } else {
        profile[item] = state.profile[item]
      }
    })
    api.put('user/update', profile).then(response => {
      commit(types.TOGGLE_LOADING_STATUS)
      // 
      if (response.data.updated) {
        Message({message: '', type: 'success', customClass: 'c-msg'})
      } else {
        Message({message: '', type: 'error', customClass: 'c-msg'})
      }
    }).catch(error => {
      commit(types.TOGGLE_LOADING_STATUS)
      Message({message: '/(o)/~~', type: 'error', customClass: 'c-msg', duration: 0, showClose: true})
      Promise.reject(error)
    })
  },
  // 
  updateAvatar({commit}, avatar) {
    commit(types.UPDATE_AVATAR, avatar)
  }
}
const mutations = {
  [types.LOAD_PROFILE](state, profile) {
    state.profile = profile
  },
  [types.UPDATE_AVATAR](state, avatar) {
    state.profile.avatar = avatar
  }
}
export default {
  state,
  getters,
  actions,
  mutations
}



