<template lang="html">
  <div class="df-register">
    <dot></dot>
    <div class="main-wrapper">
      <div class="alert">
        <el-alert
          title=":)"
          type="success"
          v-show="alertVisible"
          v-if="isSuccess"
        ></el-alert>
        <el-alert
          title="/(o)/~~"
          type="error"
          v-show="alertVisible"
          v-else
        ></el-alert>
      </div>
      <div class="main">
        <div class="form-wrapper">
          <el-form class="form c-form" ref="form" :rules="rules" :model="form">
            <el-form-item label="" prop="name">
              <el-input
                placeholder=""
                v-model="form.name"
                @blur="showPlaceholder"
                @focus="hidePlaceholder"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="email" v-if="emailVisible">
              <el-input
                v-model="form.email"
                placeholder=""
                @blur="showPlaceholder"
                @focus="hidePlaceholder"
              ></el-input>
              <!-- <el-button class="reg-validate" type="text" @click="toggleEmail"></el-button> -->
            </el-form-item>
            <el-form-item label="" prop="mobile" v-else>
              <el-input
                placeholder=""
                @blur="showPlaceholder"
                @focus="hidePlaceholder"
              ></el-input>
              <el-button class="validate" type="text" @click="toggleEmail"></el-button>
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input
                placeholder="6"
                type="password"
                v-model="form.password"
                @blur="showPlaceholder"
                @focus="hidePlaceholder"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="password_confirmation">
              <el-input
                placeholder=""
                type="password"
                v-model="form.password_confirmation"
                @focus="hidePlaceholder"
                @blur="showPlaceholder"
              ></el-input>
            </el-form-item>
            <el-form-item prop="protocol">
              <el-checkbox v-model="form.protocol">
                
                <el-button type="text" @click="showDialog"></el-button>
              </el-checkbox>
            </el-form-item>
            <el-form-item>
              <el-button
                class="publish"
                :loading="isLoading"
                :disabled="isRegistered"
                @click="submitForm"
                >
              </el-button>
            </el-form-item>
          </el-form>
        </div>
        <el-dialog class="register-dialog c-dialog" title="" v-model="dialogVisible">
          <span>()</span>
          <div class="about-user">
            <h3></h3>
            <span>1. ()</span>
            <span>2. ()</span>
            <span>3. ()</span>
            <span>4. </span>
          </div>
          <div class="copyright">
            <h3></h3>
            <span>1. ()</span>
          </div>
          <span slot="footer">
            <el-button class="btn-default" @click='showDialog'></el-button>
          </span>
        </el-dialog>
    </div>
  </div>
  </div>
</template>

<script>
import api from '@/api'
import c from '@/common/js'
import Dot from '@/components/dot'
import { mapGetters } from 'vuex'
export default {
  name: 'userRegister',
  components: {
    Dot
  },
  data() {
    return {
      form: {  // 
        name: '',
        email: '',
        password: '',
        password_confirmation: '',
        protocol: true
      },
      rules: {  // 
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          min: 2,
          max: 20,
          message: '2-20',
          trigger: 'blur'
        }, {
          type: 'string',
          pattern: /^[\u4E00-\u9FFFa-zA-Z0-9_-]{4,20}$/,
          message: '',
          trigger: 'blur'
        }, {
          validator: this.validateUsername,
          trigger: 'blur'
        }],
        email: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          max: 50,
          message: '50',
          trigger: 'blur'
        }, {
          type: 'string',
          pattern: /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/,
          message: '',
          trigger: 'blur'
        }, {
          validator: this.validateEmail,
          trigger: 'blur'
        }],
        mobile: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          len: 11,
          message: '11',
          trigger: 'blur'
        }, {
          type: 'string',
          pattern: /^1\d{10}$/,
          message: '',
          trigger: 'blur'
        }, {
          validator: this.validateMobile,
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: '',
          trigger: 'blur'
        }, {
          min: 6,
          max: 20,
          message: '6-20',
          trigger: 'blur'
        }, {
          type: 'string',
          pattern: /^[a-zA-Z0-9_-]{6,20}$/,
          message: '',
          trigger: 'blur'
        }],
        password_confirmation: [{
          validator: this.validatePassword,
          trigger: 'blur'
        }],
        protocol: [{
          validator: this.validateProtocol,
          trigger: 'change'
        }]
      },
      dialogVisible: false,  // 
      alertVisible: false,  // 
      emailVisible: true,   // email
      lastPlaceholder: '',
      isLoading: false,  //  
      isRegistered: false,  // 
      isSuccess: false   // 
    }
  },
  computed: {
    ...mapGetters([
      'isLogined'
    ])
  },
  created() {
    c.logged()
  },
  methods: {
    // 
    validateUsername(r, v, cb) {
      api.post('user/register', this.form).then(response => {
        response.data.name ? cb(new Error('')) : cb()
      })
    },
    // 
    validateEmail(r, v, cb) {
      api.post('user/register', this.form).then(response => {
        response.data.email ? cb(new Error('')) : cb()
      })
    },
    // 
    validateMobile(r, v, cb) {
      api.post('user/register', this.form).then(response => {
        response.data.mobile ? cb(new Error('')) : cb()
      })
    },
    // 
    validatePassword(rule, value, cb) {
      if (value === '') {
        cb(new Error(''))
      } else if (value !== this.form.password) {
        cb(new Error(''))
      } else {
        cb()
      }
    },
    // 
    validateProtocol(rule, value, cb) {
      value ? cb() : cb(new Error(''))
    },
    // 
    submitForm() {
      this.isLoading = true
      this.$refs['form'].validate(valid => {
        if (valid) {
          // 
          this.form.is_submit = true
          api.post('user/register', this.form).then(response => {
            // 
            if (response.data.error) {
              if (response.data.error.http_code === 400) {
                this.$message({
                  message: response.data.error.message,
                  type: 'error',
                  customClass: 'c-msg',
                  showClose: true
                })
              }
            } else if (response.data.registered) {
              // 
              this.isSuccess = true
              this.isRegistered = true
            } else {
              // 
              this.isSuccess = false
            }
            this.alertVisible = true
          }).catch(error => {
            this.$message({message: '/(o)/~~', type: 'error', customClass: 'c-msg', duration: 0, showClose: true})
            Promise.reject(error)
          })
        }
        return false
      })
      this.isLoading = false
    },
    showDialog() {
      this.dialogVisible = !this.dialogVisible
    },
    toggleEmail() {
      this.emailVisible = !this.emailVisible
    },
    showPlaceholder(e) {
      e.target.placeholder = this.lastPlaceholder
    },
    hidePlaceholder(e) {
      this.lastPlaceholder = e.target.placeholder
      e.target.placeholder = ''
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '../../common/stylus/common'

.df-register
  height 100%
  .main-wrapper
    width 100%
    .alert
      margin-top 10px
      text-align center
    .main
      fj(center,center)
      .form-wrapper
        width 33.3%
        .form
          .publish
            width 100%
      .register-dialog
        .about-user,.copyright
          &>span
            display inline-block
            line-height 2em
            color Silver
</style>



